import React from "react";
import { Container, Typography, Grid } from "@material-ui/core";
import { Field } from "formik";

import PhotoUploadItemDonee from "./PhotoUploadItemDonee";

const PhotoVerificationDonee = () => {
  return (
    <Container maxWidth="md">
      <Typography variant="body1" gutterBottom>
        Upload a digital photo of the ngo director ID
      </Typography>
      <Grid container spacing={3} sx={{ paddingTop: 3, paddingBottom: 2 }}>
        <Grid item xs={12} sm={6} md={6}>
          <Field
            component={PhotoUploadItemDonee}
            name="id_front"
            label="ID front photo"
          />
        </Grid>
        <Grid item xs={12} sm={6} md={6}>
          <Field
            component={PhotoUploadItemDonee}
            name="id_back"
            label="ID back photo"
          />
        </Grid>
      </Grid>
    </Container>
  );
};

export default PhotoVerificationDonee;
